// Main Style
@import 'layout/variable_mixin_function';

// --------------------------------------------------------

/* Vendor */

// --------------------------------------------------------

/* Component */
@import './components/pd-list';

// --------------------------------------------------------

/* Container */

.pd-list-global {
	.sec-title-box {
		align-items: flex-start;
	}
}

// 無搜尋結果
.pd-list-null {
	// margin-top: rem-calc(80);
	// margin-bottom: rem-calc(190);
	margin-top: 12vh;
	margin-bottom: 14vh;
	text-align: center;

	@include breakpoint(medium) {
		margin-top: rem-calc(115);
		margin-bottom: rem-calc(70);
	}

	&__img-box {
		margin: 0 auto;
		margin-bottom: rem-calc(24);
		width: rem-calc(80);

		@include breakpoint(medium) {
			margin-bottom: rem-calc(42);
			width: rem-calc(113);
		}
	}

	&__img {
		width: 100%;
		height: auto;
	}

	&__desc {
		font-size: rem-calc(16);
		color: #333;

		@include breakpoint(medium) {
			font-size: rem-calc(20);
		}
	}
}

// --------------------------------------------------------

// 商品列表-賣家店鋪資訊
.seller-info {
	margin-bottom: rem-calc(40);

	@include breakpoint(medium only) {
		margin-bottom: rem-calc(80);
	}

	// 主要資訊
	&__main {
		display: flex;
	}

	&__img-box {
		$width: rem-calc(80);

		flex: 0 0 $width;
		margin-right: rem-calc(12);
		width: $width;
		border-radius: 20px;
		border: solid 1px #edf0f3;
	}

	&__text-box {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__name {
		font-weight: $font-bold-b;
		margin-bottom: rem-calc(1);
	}

	&__amount {
		margin-bottom: 0;
	}

	// =====================

	// Banner
	&__banner-box {
		margin-top: rem-calc(20);
		margin-bottom: rem-calc(18);
		border-radius: rem-calc(12);

		&::before {
			@include before-img-size(340, 160);
		}

		@include breakpoint(medium) {
			margin-top: rem-calc(16);
			margin-bottom: rem-calc(24);
		}
	}

	// =====================

	// 敘述
	&__desc {
		margin-top: rem-calc(16); // 避免哪天沒有圖，補個margin
		font-size: rem-calc(15);

		@include breakpoint(large) {
			font-size: rem-calc(18);
		}
	}
}

// --------------------------------------------------------

// 官方旗艦店
.pd-list-flagship {
	&__l {
		@include breakpoint(large) {
			position: relative;
			padding-right: rem-calc(20);
		}
	}

	.seller-info {
		&__name {
			@include breakpoint(medium) {
				font-size: rem-calc(24);
			}
		}

		@include breakpoint(large) {
			&.is-fixed {
				position: fixed;
				top: 98px;
				// width: calc((100% - #{100px * 2}) * #{(4 / 12)} - #{rem-calc(20)} - #{rem-calc($gutter-m-half)});

				@include breakpoint(1140 + 100 * 2) {
					// width: calc(1140px * #{(4 / 12)} - #{rem-calc(20)} - #{rem-calc($gutter-m-half)});
				}
			}

			&.is-fixed-bottom {
				position: absolute;
				top: auto;
				bottom: 0;
				left: 0;
				margin-bottom: 0;
				// width: auto;
			}
		}
	}

	.pd-list__cont {
	}
}
