// 針對 產品列表的部分設置字級

// 產品列表下的物件大小
// * SIT F127 重新調整產品列的文字大小
// * 避免影響到部分區塊，因此如此設置(not....)
.pd-list {
	.pd-card {
		&:not(.pd-card--hori-with-price) {
			.pd-card {
				&__title {
					font-size: rem-calc(15);

					@include breakpoint(medium) {
						font-size: rem-calc(15);
					}
				}

				&__price {
					font-size: rem-calc(16);

					@include breakpoint(medium) {
						font-size: rem-calc(18);
					}
				}
			}
		}
	}
}
